import React from "react"

import Layout from "../components/layout"
import emoji from 'emoji-dictionary'
import "../assets/main.css"
import {StaticQuery} from "gatsby";
import Markdown from "react-markdown";
import breaks from 'remark-breaks'
const emojiSupport = text => text.value.replace(/:\w+:/gi, name => emoji.getUnicode(name))
const IndexPage = () => (
<Layout title="Home">
  <StaticQuery
    query={graphql`
        query {
          allStrapiArticle(sort: {fields: published_at, order: DESC}, filter: {published: {eq: true}}){
            edges {
              node {
                strapiId
                Content
                Title
                published_at
                image {
                  publicURL
                }
              }
            }
          }
        }
      `}
    render={data => (  <div style={{
        width: '100%',
        display: 'flex',
        justifyContent: 'center'
      }}>
        <div style={{
          width: '100%',
          maxWidth: '700px'
        }}>
          {data.allStrapiArticle.edges.map(({node: article}) => (
            <article style={{marginBlockEnd:70}} key={article.id}>
              <h1>{article.Title}</h1>
              <img alt={article.Title} style={{
                width: '100%',
                marginBlockEnd:20
              }} src={article.image.publicURL}/>
              <Markdown source={article.Content} plugins={[breaks]} renderers={{text:emojiSupport}}/>
              <p
              style={{
                color:"#8e8e8e"
              }}>{(new Date(article.published_at)).toLocaleDateString()}</p>
              <div style={{
                marginBlock:50,
                width: '100%',
                height:2,
                backgroundColor:'black'
              }}/>
            </article>
          ))}
        </div>
      </div>
    )}
  />
</Layout>)

export default IndexPage
